.filter {
    width: 200px;
    padding: 10px;
    background-color: green;
}

.filter-title {
    margin: 0;
    font-size: 20px;
    margin-bottom: 10px;
}

.filter-list {
    list-style: none;
    margin: 0;
    padding: 0;

    padding-left: 10px;
}

.filter-item {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

