.products {
    list-style: none;
    padding: 0;
    margin: 0 auto;
    width: 330px;
    display: grid;
    grid-template-columns: repeat(1, 300px);

    grid-gap: 15px;
}

@media screen and (min-width: 768px) {
    .products {
        width: 620px;
        grid-template-columns: repeat(2, 300px);
        grid-gap: 20px;
    }
}

@media screen and (min-width: 1280px) {
    .products {
        width: 930px;
        grid-template-columns: repeat(3, 300px);
        grid-gap: 15px;
    }
}

.products__descr,
.products__model {
    margin: 0;
}

.products__item {
    margin: 0;
    padding: 5px;
    background-color: #212121;
    overflow: hidden;
}

.products__image-wrapper {
    position: relative;
}

.products__sale {
    display: none;
    position: absolute;
    right: -28px;
    top: -8px;
    transform: rotate(45deg);
    width: 100px;
    text-align: center;
    font-size: 20px;
    background-color: yellow;
}

.products__sale--active {
    display: block;
}

.products__image {
    display: block;
    width: 100%;
}

.products__descr {
    padding: 10px;
    color: bisque;
}

.products__btn-buy {
    font-size: 24px;
    padding: 10px 20px;
    border: none;
    background-color: burlywood;
    color: rgb(58, 44, 44);
    border-radius: 3px;
}

.products__btn-buy:active {
    transform: scale(0.95);
}